import React from "react";
import { Container, Infobox, Header, Contact } from "./Service.styles";
import { Link } from "react-router-dom";

function Servicebody() {
  return (
    <Container>
      <Header>
        <h1>Nasze usugi</h1>
      </Header>
      <Infobox>
        <h3>Sprowadzenia auta z usa lub kanady</h3>
        <p>
          Jestemy jednym z wiodcych na polskim rynku importerw aut, motocykli
          i quadw z terenu USA oraz Kanady. Za porednictwem naszej strony
          internetowej otrzymasz kompleksow pomoc w sprowadzeniu samochodu ze
          Stanw Zjednoczonych.
        </p>
      </Infobox>
      <Infobox>
        <h3>Transport auta pod dom</h3>
        <p>
          Przy wyborze naszej firmy masz moliwo odebra pojazd bez wychodzenia z domu! Wystarczy ,e wybierzesz opcje z dostarczeniem pojazdu pod dom. Dostarczymy twoje wymarzone auto pod wskazany adres, a Ty bdziesz mg sie nim cieszy bez martwienia si jak je przetransportowa z portu.
        </p>
      </Infobox>
      <Infobox>
        <h3>Naprawa</h3>
        <p>
          W naszej ofercie jestemy w stanie zaoferowa napraw sprowadzonego auta w zaprzyjanionym serwisie mechanicznym. Przedstawimy Ci wycen naprawy, otrzymasz faktury na wszystkie czci i bdziesz mg odebra ju naprawiony pojazd ,ktrym bdziesz mg odrazu wyjecha na ulice!
        </p>
      </Infobox>
      <Contact>
        <Link to="/kontakt">
          <button>Kontakt</button>
        </Link>
      </Contact>
    </Container>
  );
}

export default Servicebody;
